.boothItemOption {
  background-color: white;
  margin: 1%;
  padding: 5%;
  border-radius: 5px;
  color: #005191;
  height: 100%;
  display: flex;
  flex-direction: column;
}

.boothItemOption > .section {
  margin: 3.5% 0;
  padding: 0;
}

.boothItemIcon {
  height: 3em;
  margin-right: 5%;
  /* padding-top: 4%; */
}
.section:nth-of-type(1) {
  margin-bottom: 8%;
  display: flex;
  line-height: 1;
}
.section:nth-of-type(2) {
  /* height: 30%; */
}
.section:nth-of-type(3) {
  height: 20%;
}
.section:nth-of-type(4) {
  height: 15%;
}

.section > .customButton {
  padding: 3%;
  margin: auto;
  width: 50%;

  text-align: center;

  font-family: 'Roboto Condensed';
  font-size: 1em;
}
